@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  font-family: 'outfit';
  overflow-x: hidden;
  display: -ms-grid;
  display: grid;
  width: 100vw;
  height: 100vh;
  place-items: center;
  background-color: #0d192b;
}

.card {
  width: 375px;
  height: 500px;
  background-color: #14253d;
  box-shadow: 1px 5px 12px 20px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 20px 20px;
}

.card :hover {
  cursor: pointer;
}

.card .image {
  width: 100%;
  height: 50%;
  position: relative;
}

.card .image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.card .image .box {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 255, 255, 0.371);
  position: absolute;
  top: 0;
}

.card .image .box img {
  width: 30px;
  height: 30px;
  opacity: 1 !important;
}

.card .text-group {
  font-size: 12px;
}

.card .text-group .title {
  color: white;
  font-weight: 600;
}

.card .text-group .para {
  font-size: 18px;
  font-weight: 300;
  color: #8bacda;
}

.card .ratings {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.card .ratings .currency {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.card .ratings .currency img {
  margin-right: 10px;
}

.card .ratings .currency span {
  color: #00fff7;
  font-weight: 600;
}

.card .ratings .time {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.card .ratings .time img {
  margin-right: 10px;
}

.card .ratings .time span {
  color: #8bacda;
  font-weight: 400;
}

.hr_line {
  height: .2px;
  border: none;
  background-color: #8bacda;
  margin-bottom: 20px;
}

.profile {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.profile .profile_img {
  width: 40px;
  height: 40px;
  margin-right: 20px;
}

.profile .profile_img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border: 1px solid white;
  border-radius: 50%;
}

.profile .profile_text p {
  color: #8bacda;
}

.profile .profile_text p b {
  font-style: italic;
}
